import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {  PanelHeader, Avatar, Div, HeaderButton} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon24Back from '@vkontakte/icons/dist/24/back';
class Begin extends React.Component{

render(){
	return(
 <Div align="center">
 <PanelHeader left={<HeaderButton onClick={() => this.setState({activePanelMap:'map'})} ><Icon24Back /></HeaderButton>}>
{this.props.selectedPlace.name}
 </PanelHeader>
<Avatar src={this.props.selectedPlace.img_1} size={200}/>
{this.props.selectedPlace.text}
</Div>
	);}

}
export default Begin;
